import React from "react"
import { Redirect, Route } from "react-router-dom"

const ProtectedRoute = ({children, ...rest})=>{
    return(
        <Route 
        {...rest}
            render={({location})=>{
                if(localStorage.getItem("status") === "employee"){
                    return (children) 
                }
                else{
                    return <Redirect
                    to={{
                        pathname: "/login",
                        state:{
                            from: location
                        }
                    }}
                    />
                }
                }
            }
        />
    )
}
{/*return <Component {...props} />*/}
export default ProtectedRoute