import React,{Fragment} from 'react';
import './Student.css'
import '../Component/Employee/Navbar.css'
import { withRouter } from "react-router";
import { Route, Switch, Redirect, Link} from 'react-router-dom'
import Navbar from '../Component/Employee/Navbar'
import Footer from '../Component/Employee/Footer'


const Student = (props) => {
    return (
        <>
            <Navbar {...props} />
            <section class="page-content">
            <Switch>
                <Route path={`${props.match.path}/`} exact component={Home} /> 
                <Route path={`${props.match.path}/setting`} exact component={Setting} />
                <Route path="*" exact render={()=><Redirect to="/student" />} />
              </Switch>
                <Footer />
            </section>
        </>
    )
}

const Home = () => {

    return (
       <Fragment>
           <h2>Home Student</h2>
       </Fragment>
    )
}

const Setting = () => {

    return (
       <Fragment>
           <h2>Setting Student</h2>
       </Fragment>
    )
}


export default withRouter(Student)